@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;500;600;700;800;900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Nunito Sans', sans-serif;
}

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 120px;
}


.header img{
    width: 100px;

}
.header ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    gap: 20px;
}
.header ul li{
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

.cardsection{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    padding: 0px 120px;
    row-gap: 40px;
}


.card{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    width: 290px;
}
.card:hover{
    transform: scale(0.98,0.98);
    transition: 0.2s ;
    box-shadow: 6px 6px 8px  rgba(196, 177, 177, 0.2);
}
.card .img-container{
    width: 290px;
    height: 210px;
    
}
.card img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 16px;
}
.card .desc{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}
